<template>
  <div id="mobileUserAdd">
    <Modal
      v-model="mobileUserBuild"
      title="新建移动端用户"
      @on-cancel="reset"
    >
      <Form ref="add" :model="AddData" :rules="AddRule" :label-width="90" style="padding:0 50px;">
        <FormItem prop="sa008" label="用户名：">
          <Input v-model.trim="AddData.sa008" placeholder="请输入用户名" size="default"/>
        </FormItem>
        <FormItem prop="sa009" label="密码：">
          <Input type="password" password v-model.trim="AddData.sa009" placeholder="请输入密码" size="default"/>
        </FormItem>
        <FormItem prop="sa019" label="真实姓名：">
          <Input v-model.trim="AddData.sa019" placeholder="请输入真实姓名" size="default"/>
        </FormItem>
        <FormItem prop="sa012" label="手机号码：">
          <Input v-model.trim="AddData.sa012" placeholder="请输入手机号码" size="default"/>
        </FormItem>
        <FormItem prop="sa015" label="所属部门：">
          <Input v-model.trim="AddData.sa015" placeholder="请输入所属部门" size="default"/>
        </FormItem>
        <FormItem prop="sa011" label="性别：">
          <RadioGroup v-model="AddData.sa011">
            <Radio label="0">男</Radio>
            <Radio label="1">女</Radio>
          </RadioGroup>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="addSubmit">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { insertMobileUser } from '@/api/systemManage'
export default {
  name: 'mobileUserAdd',
  data () {
    return {
      /* 添加数据 */
      AddData: {
        sa008: '',
        sa012: '',
        sa019: '',
        sa009: '',
        sa015: '',
        sa011: '',
        unitList: [1]
      },
      AddRule: {
        sa008: [
          { required: true, message: '用户名不能为空', trigger: 'blur' }
        ],
        sa012: [
          { required: true, message: '手机号不能为空', trigger: 'blur' },
          { pattern: /^1[34578]\d{9}$/, message: '请填写正确的手机号码', trigger: 'blur' }
        ],
        sa009: [
          { required: true, message: '密码不能为空', trigger: 'blur' }
        ],
        sa015: [
          { required: true, message: '所属部门不能为空', trigger: 'blur' }
        ]
      },
      /* 新建框 */
      mobileUserBuild: false
    }
  },
  methods: {
    /* axios: 添加数据 */
    addSubmit () {
      this.$refs.add.validate(valid => {
        if (valid) {
          insertMobileUser({
            data: {
              sa008: this.AddData.sa008,
              sa012: this.AddData.sa012,
              sa019: this.AddData.sa019,
              sa009: this.AddData.sa009,
              sa015: this.AddData.sa015,
              sa011: this.AddData.sa011,
              unitList: [1]
            }
          }).then(res => {
            if (res.data.code === '0000') {
              this.$emit('getTableData')
              this.$Notice.success({ title: '新建移动端用户成功' })
              this.mobileUserBuild = false
              this.AddData = {}
              this.reset()
            } else {
              this.$Notice.error({ title: res.data.msg })
            }
          }).catch(() => {
            this.$Notice.error({ title: '服务器错误' })
          })
        }
      })
    },
    // 重置表單參數
    reset () {
      this.AddData = {
        sa008: '',
        sa012: '',
        sa019: '',
        sa009: '',
        sa015: '',
        sa011: '',
        unitList: [1]
      }
      this.$refs.add.resetFields()
    }
  }
}
</script>

<style scoped lang="scss">
</style>
