<template>
  <div id="mobileUserManagement">
    <div class="queryBox">
      <QueryBox :formArr="queryFrom" @query="getQueryData" style="width: calc(100% - 65px)" ref="getQuery"></QueryBox>
      <div class="queryBtns">
        <Button type="success" size="small" icon="md-add" class="btn-add" @click.stop="$refs.mobileUserAdd.mobileUserBuild = true">新建</Button>
      </div>
    </div>
    <div class="content_box">
      <div>
        <Table border stripe :loading="loading" :columns="columns1" :data="data1" highlight-row size="small" :max-height="tableHeight"></Table>
        <div class="page_box" id="pageBox">
          <Page :total="total" ref="pages" :page-size="pageSize" :current.sync="pageNum" size="small" @on-change="pageChange" show-total show-elevator/>
        </div>
      </div>
    </div>
    <!-- 新建-->
    <mobileUserAdd ref="mobileUserAdd" @getTableData="getClearQueryData"></mobileUserAdd>
    <!-- 修改-->
    <mobileUserEdit ref="mobileUserEdit" :editDefaultData="editDefaultData" @getTableData="getTableList"></mobileUserEdit>
  </div>
</template>

<script>
import QueryBox from '@/components/publicQuery'
import mobileUserAdd from './mobileUserAdd'
import mobileUserEdit from './mobileUserEdit'
import {
  // 表格列表
  getMobileListUser,
  // 删除单条记录
  delMobileUser
} from '@/api/systemManage'
export default {
  name: 'logManagement',
  data () {
    return {
      /* 表格高度 */
      tableHeight: window.innerHeight - 290,
      /* 查询框查询条件 */
      queryData: {},
      /* 表格数据 */
      columns1: [
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.pageNum - 1) * this.pageSize + 1)
          }
        },
        {
          title: '用户名',
          align: 'center',
          key: 'sa008'
        },
        {
          title: '手机号码',
          align: 'center',
          key: 'sa012'
        },
        {
          title: '真实姓名',
          align: 'center',
          key: 'sa019'
        },
        {
          title: '所属部门',
          align: 'center',
          key: 'sa015'
        },
        {
          title: '操作',
          key: '',
          width: 180,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              // 修改
              h('Button', {
                props: {
                  type: 'warning',
                  size: 'small',
                  icon: 'md-create'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.editDefaultData = params.row
                    this.$refs.mobileUserEdit.mobileUserRebuild = true
                  }
                }
              }, '修改'),
              // 删除
              h('Button', {
                props: {
                  type: 'error',
                  size: 'small',
                  icon: 'md-trash'
                },
                on: {
                  click: () => {
                    this.$Modal.confirm({
                      title: '确认',
                      content: '请问您确定要删除该移动端用户吗？',
                      onOk: () => {
                        this.infoDel(params.row.sa001)
                      },
                      onCancel: () => {
                      }
                    })
                  }
                }
              }, '删除')
            ])
          }
        }
      ],
      data1: [],
      /* 总条数 */
      total: 0,
      pageNum: 1,
      pageSize: Math.floor((window.innerHeight - 290) / 40) - 1,
      /* 加载 */
      loading: false,
      /* 编辑框默认原数据 */
      editDefaultData: {}
    }
  },
  components: {
    QueryBox,
    mobileUserAdd,
    mobileUserEdit
  },
  computed: {
    // 查询表单参数
    queryFrom: function () {
      return [
        {
          type: 1,
          field: 'sa008',
          title: '用户名'
        },
        {
          type: 1,
          field: 'sa012',
          title: '手机号码'
        }
      ]
    }
  },
  created () {
    this.getTableList()
  },
  mounted () {
    // 窗口改变时table自适应
    window.addEventListener('resize', () => {
      this.tableHeight = window.innerHeight - 290
      this.pageSize = Math.floor((window.innerHeight - 290) / 40) - 1
      this.getTableList()
    }, false)
  },
  methods: {
    /* 获取查询数据 */
    getQueryData (data) {
      this.queryData = data
      this.pageNum = 1
      this.getTableList()
    },
    /* 获取字段列表 */
    getTableList () {
      this.loading = true
      getMobileListUser({
        data: {
          pageSize: this.pageSize,
          pageNumber: this.pageNum,
          ...this.queryData
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.data1 = res.data.data.rows
          this.total = res.data.data.total
          this.loading = false
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 分页跳转 */
    pageChange (pageNum) {
      this.pageNum = pageNum
      this.getTableList()
    },
    /* axios: 删除数据 */
    infoDel (e) {
      delMobileUser({
        data: {
          sa001: e
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.$Notice.success({ title: '删除成功' })
          // 避免刪除当前页最后一条数据时显示--无数据
          if (this.data1.length === 1 && this.pageNum > 1) {
            this.pageNum = this.pageNum - 1
          }
          this.getTableList()
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 新添加任务之后返回的任务列表同时清空查询条件 */
    getClearQueryData () {
      this.queryData = {}
      this.pageNum = 1
      this.getTableList()
      this.$refs.getQuery.cleardata()
    }
  }
}
</script>

<style scoped lang="scss">
  #mobileUserManagement {
    position: relative;
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      .queryBtns {
        float: right;
        margin-top: 14px;
      }
    }
    .content_box {
      margin: 0 15px 0;
      .page_box{
        width: 100%;
        padding: 15px;
        text-align: right
      }
    }
    .btn-add {
      background-color: #01c296;
      border-color: #01c296;
      &:hover, &:focus {
        background-color: #05ab85;
        border-color: #05ab85;
      }
    }
  }
</style>
