<template>
  <div id="mobileUserEdit">
    <Modal
      v-model="mobileUserRebuild"
      title="编辑移动端用户">
      <Form ref="edit" :model="EditData" :rules="EditRule" :label-width="90" style="padding:0 50px;">
        <FormItem prop="sa001" label="ID：" style="display: none">
          {{EditData.sa001}}
        </FormItem>
        <FormItem prop="sa008" label="用户名：">
          <Input v-model.trim="EditData.sa008" placeholder="请输入用户名" size="default"/>
        </FormItem>
        <FormItem prop="sa009" label="密码：">
          ********
        </FormItem>
        <FormItem prop="sa019" label="真实姓名：">
          <Input v-model.trim="EditData.sa019" placeholder="请输入真实姓名" size="default"/>
        </FormItem>
        <FormItem prop="sa012" label="手机号码：">
          <Input v-model.trim="EditData.sa012" placeholder="请输入手机号码" size="default"/>
        </FormItem>
        <FormItem prop="sa015" label="所属部门：">
          <Input v-model.trim="EditData.sa015" placeholder="请输入所属部门" size="default"/>
        </FormItem>
        <FormItem prop="sa011" label="性别：">
          <RadioGroup v-model="EditData.sa011">
            <Radio label="0">男</Radio>
            <Radio label="1">女</Radio>
          </RadioGroup>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="editSubmit">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { editMobileUser } from '@/api/systemManage'
export default {
  name: 'mobileUserEdit',
  data () {
    return {
      EditRule: {
        sa008: [
          { required: true, message: '用户名不能为空', trigger: 'blur' }
        ],
        sa012: [
          { required: true, message: '手机号不能为空', trigger: 'blur' },
          { pattern: /^1[34578]\d{9}$/, message: '请填写正确的手机号码', trigger: 'blur' }
        ],
        sa015: [
          { required: true, message: '所属部门不能为空', trigger: 'blur' }
        ]
      },
      /* 编辑框 */
      mobileUserRebuild: false,
      /* 编辑数据 */
      EditData: {}
    }
  },
  props: {
    /* 编辑行原数据 */
    editDefaultData: {}
  },
  watch: {
    editDefaultData () {
      this.dataHandle()
    }
  },
  mounted () {
  },
  methods: {
    /* 编辑框默认值数据处理 */
    dataHandle () {
      this.EditData.sa019 = this.editDefaultData.sa019
      this.EditData.sa009 = this.editDefaultData.sa009
      this.EditData.sa015 = this.editDefaultData.sa015
      this.EditData.sa001 = this.editDefaultData.sa001
      this.EditData.sa011 = String(this.editDefaultData.sa011)
      this.$set(this.EditData, 'sa008', this.editDefaultData.sa008)
      this.$set(this.EditData, 'sa012', this.editDefaultData.sa012)
    },
    /* 编辑移动端用户 */
    editSubmit () {
      this.$refs.edit.validate(valid => {
        if (valid) {
          editMobileUser({
            data: {
              sa008: this.EditData.sa008,
              sa012: this.EditData.sa012,
              sa019: this.EditData.sa019,
              sa009: this.EditData.sa009,
              sa015: this.EditData.sa015,
              sa001: this.EditData.sa001,
              sa011: this.EditData.sa011,
              unitList: [1]
            }
          }).then(res => {
            if (res.data.code === '0000') {
              this.$Notice.success({ title: '编辑移动端用户成功' })
              this.$emit('getTableData')
              this.mobileUserRebuild = false
              this.data = {}
              this.reset()
            } else {
              this.$Notice.error({ title: res.data.msg })
            }
          })
        }
      })
    },
    // 重置表單參數
    reset () {
      this.EditData = {
        sa008: '',
        sa012: '',
        sa019: '',
        sa009: '',
        sa015: '',
        sa001: '',
        sa011: '',
        unitList: [1]
      }
      this.$refs.edit.resetFields()
    }
  }
}
</script>

<style scoped lang="scss">
</style>
